.App {
  text-align: center;
}

.app-container {
  width: 80%;
  margin-right: auto;
  margin-left: auto;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.head-content {
  font-size: 35px;
  color: #F5F3F3;
  margin-bottom: 100px;
}

.head3 {
  color: #EEEEEE;
  font-size: 25px;
}

.p-desc {
  font-size: 20px;
  color: #EEEEEE;
}

iframe {
  height: 100%;
  width: 100%;
  border: none;
}

.task-container {
  display: flex;
  margin: 0 auto 60px auto;
}

.videos {
  width: 60%; 
  height: 400px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #F5F3F3;
}

.info-view {
  margin-right: 50px;
}

.info-container {
  margin-top: 20%;
  margin-bottom: 20%;
  vertical-align: middle;

}

a {
  text-decoration: none;
  margin-right: 5vmin;
  color: #F5F3F3;
}

a:active {
  color: #FF9400;
}

.header {
  background-color: rgba(20, 20, 20, 0.4);
  overflow: hidden;
  height: 50px;
  width: 100%;
  top: 0;
}

.head {
  display: inline;
  margin: 0 20% 0 20%;
  height: 100%;
  display: flex;
  flex-direction: row;
}

.logo {
  color: #EEE;
  font-size: 40px;
}

.zenbit-logo {
  align-content: left;
  display: inline;
}

.z-links {
  display: inline;
  margin: auto 0 auto auto;
  align-content: right;
  font-size: 20px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
